ca, Verdana, Geneva, Tahoma, sans-serif;\n}\n\n.header {\n    background-color: var(--header-bg-clr);\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    grid-area: header;\n    \n    position: sticky;\n    top: 0;\n\n    z-index: 3;\n\n    padding: 0 24px;\n\n    display: flex;\n    justify-content: space-between;\n    \n    & h1 {\n        color: var(--text-white);\n\n        display: flex;\n        gap: 5px;\n\n        & svg {\n            height: 40px;\n            width: 40px;\n            \n            fill: var(--text-white);\n        }\n    }\n\n    & button {\n        width: 150px;\n        height: 60%;\n\n        font-size: x-large;\n\n        padding: 8px 12px;\n\n        background-color: var(--accent-color);\n        color: var(--text-white);\n\n        border: 2px solid var(--border-white);\n        border-radius: 12px;\n\n        &:hover,\n        &:focus {\n            border-color: var(--focus-blue);\n            box-shadow: var(--shadow-light);\n            cursor: pointer;\n        }\n\n    }\n}\n\n\n.cards {\n    grid-area: cards;\n\n    padding: 50px 45px;\n\n    display: grid;\n    grid-template-rows: repeat(3, 300px);\n    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n    gap: 38px;\n\n    background-color: var(--accent-color);\n\n\n    & .card {\n        background-color: var(--card-bg-clr);\n\n        border: 2px solid var(--border-white);\n        border-radius: 12px;\n\n        color: var(--text-white);\n        \n        display: grid;\n        grid-template: 1fr 60px / 1fr;\n        grid-template-areas: \n            \"info\"\n            \"options\";\n        gap: 10px;\n\n\n        &:hover,\n        &:focus {\n            border-color: var(--focus-blue);\n            box-shadow: var(--shadow-light);\n        }\n\n\n        & .info {\n            display: grid;\n            grid-template: 2fr 1fr 1fr / 1fr;\n            grid-template-areas:\n                \"title\"\n                \"author\"\n                \"pages\";\n            gap: 10px;\n            \n            padding: 30px;\n\n            & .title {\n                grid-area: title;\n                justify-self: start;\n                \n                font-family: 'allerbold';\n                font-size: 1rem;\n\n                width: 100%;\n\n                display: flex;\n                flex-wrap: wrap;\n                word-wrap: break-word;\n                overflow: hidden;\n            }\n    \n            & .author {\n                grid-area: author;\n    \n                font-size: 1rem;\n                \n                color: var(--text-secondary);\n\n                display: flex;\n                flex-wrap: wrap;\n                word-wrap: break-word;\n                overflow: hidden;\n                \n            }\n    \n            & .pages {\n                grid-area: pages;\n                \n                font-size: 14px;\n\n                color: var(--text-secondary);\n\n                display: flex;\n                flex-wrap: wrap;\n                word-wrap: break-word;\n                overflow: hidden;\n            }\n        }\n\n        & .options {\n            display: grid;\n            grid-template: 1fr / 1fr 1fr;\n            grid-template-areas:\n                \"delete read\";\n\n            \n            border-top: 2px solid var(--border-white) ;            \n\n\n            & > .delete {\n                width: 100%;\n                height: 100%;\n\n                border-right: 2px solid var(--border-white);\n\n                \n                & button {\n                    border: none;\n                    \n                    width: 100%;\n                    height: 100%;\n\n                    color: aliceblue;\n\n                    font-weight: bold;\n                    font-size: 20px;\n                    \n                    background-color: var(--delete-clr);\n                    opacity: 0.5;\n\n                    border-bottom-left-radius: 10px;\n\n                    display: flex;\n                    gap: 5px;\n                    flex-direction: row-reverse;\n                    justify-content: center;\n                    align-items: center;\n                    \n\n                    & .icon {\n                        fill: aliceblue;\n                        height: 35px;\n                        width: 35px;\n\n                        position: relative;\n                        top: -2px;\n\n                    }\n\n                    &:hover,\n                    &:focus {\n                        cursor: pointer;\n                        opacity: 1;\n                    }\n                }\n\n           }\n\n           & > .read {\n                width: 100%;\n                height: 100%;\n\n\n                & button {\n                    border: none;\n                    \n                    width: 100%;\n                    height: 100%;\n\n                    border-bottom-right-radius: 10px;\n\n                    background-color: var(--read-clr);\n\n                    opacity: 0.5;\n\n                    color: aliceblue;\n\n                    font-weight: bold;\n                    font-size: 20px;\n\n                    display: flex;\n                    gap: 5px;\n                    flex-direction: row-reverse;\n                    justify-content: center;\n                    align-items: center;\n\n\n                    & .icon {\n                        fill: aliceblue;\n                        height: 35px;\n                        width: 35px;\n\n                        position: relative;\n                        top: -2px;\n                    }\n\n                    &:hover,\n                    &:focus {\n                        cursor: pointer;\n                        opacity: 1;\n                    }\n\n                    &#unread {\n                        background-color: var(--unread-clr);\n                    }\n\n                }\n\n            }\n\n        }\n\n    }\n}\n\n\ndialog.add-book-modal[open] {\n    width: 30vw;\n    min-width: 330px;\n    height: 400px;\n\n    margin: auto;\n    padding: 0;\n\n    background-color: var(--card-bg-clr);\n\n    border: 2px solid var(--focus-blue);\n    border-radius: 14px;\n\n    box-shadow: var(--shadow-light);\n    \n    overflow: hidden;\n\n    &::backdrop {\n        backdrop-filter: blur(4px);\n    }\n\n\n    & form {\n        width: 100%;\n        height: 100%;\n    \n\n        & .card {\n            --focus-blue: #4285F4;\n\n            background-color: var(--header-bg-clr);\n\n            width: 100%;\n            height: 100%;\n    \n            color: var(--text-white);\n            \n            display: grid;\n            grid-template: 1fr 60px / 1fr;\n            grid-template-areas: \n                \"info\"\n                \"options\";\n            \n            \n    \n            & .info {\n                display: grid;\n                grid-template: 3fr 2fr 1fr 1fr / 1fr;\n                grid-template-areas:\n                    \"title\"\n                    \"author\"\n                    \"pages\"\n                    \"read\";\n                gap: 10px;\n                \n                padding: 24px;\n\n    \n                & .title {\n                    grid-area: title;\n                    justify-self: start;\n                    \n                    width: 100%;\n                    font-size: 1rem;\n\n                    & > label[for=\"title\"] {\n                        position: absolute;\n                        top: 78px;\n                        right: 40px;\n\n                        font-size: 14px;\n\n                        color: var(--text-secondary);\n                    }\n\n                    \n                    & > input {\n                        color: var(--text-white);\n                        \n                        background-color: var(--card-bg-clr);\n                        \n                        height: 70%;\n                        width: 100%;\n\n                        font-size: 26px;\n\n                        padding: 5px 10px;\n                        \n                        border: 2px solid var(--border-white);\n                        border-radius: 12px;\n                        \n                        outline: none;\n                        \n                        &:focus,\n                        &:hover {\n                            border-color: var(--focus-blue);\n                        }\n                        \n                        &:invalid {\n                            border-color: var(--delete-clr);\n                            box-shadow: 0 0 2px var(--delete-clr);\n                        }\n\n                    }\n                }\n        \n                & .author {\n                    grid-area: author;\n        \n                    font-size: 1rem;\n                    \n                    color: var(--text-secondary);\n\n                    \n                    & > input {\n                        color: var(--text-white);\n\n                        background-color: var(--card-bg-clr);\n\n                        height: 70%;\n                        width: 100%;\n\n                        font-size: 20px;\n\n                        padding: 5px 10px;\n\n                        border: 2px solid var(--border-white);\n                        border-radius: 12px;\n\n                        outline: none;\n\n                        &:focus,\n                        &:hover {\n                            border-color: var(--focus-blue);\n                        }\n\n                    }\n                    \n                }\n        \n                & .pages {\n                    grid-area: pages;\n    \n                    color: var(--text-secondary);\n\n                    display: flex;\n                    justify-content: center;\n\n                    & > input {\n                        color: var(--text-white);\n\n                        background-color: var(--card-bg-clr);\n\n                        height: 100%;\n                        width: 50%;\n\n                        font-size: 14px;\n\n                        padding: 5px 10px;\n\n                        border: 2px solid var(--border-white);\n                        border-radius: 12px;\n\n                        outline: none;\n\n                        &:focus,\n                        &:hover {\n                            border-color: var(--focus-blue);\n                        }\n\n                    }\n                }\n\n                & .read {\n                    grid-area: read;\n                    align-self: end;\n                    \n                    font-size: 14px;\n    \n                    color: var(--text-secondary);\n\n                    padding: 0 50px;\n\n                    display: flex;\n                    gap: 20px;\n                    justify-content: center;\n                    align-items: center;\n                    \n\n                    & > input {\n                        width: 24px;\n                        height: 24px;\n\n                    }\n                }\n            }\n    \n            & .options {\n                display: grid;\n                grid-template: 1fr / 1fr 1fr;\n                grid-template-areas:\n                    \"delete read\";\n    \n                \n                border-top: 2px solid var(--border-white) ;            \n    \n    \n                & > .cancel {\n                    width: 100%;\n                    height: 100%;\n                    \n                    border-right: 2px solid var(--border-white);\n    \n                    \n                    & button {\n                        border: none;\n                        \n                        width: 100%;\n                        height: 100%;\n                        \n                        background-color: var(--delete-clr);\n                        opacity: 0.5;\n\n                        border-bottom-left-radius: 10px;\n    \n                        color: aliceblue;\n    \n                        font-weight: bold;\n                        font-size: 20px;\n\n                        display: flex;\n                        justify-content: center;\n                        align-items: center;\n                        gap: 5px;\n\n                        & svg {\n                            height: 35px;\n                            width: 35px;\n                            \n                            fill: aliceblue;\n                        }\n                        \n                        &:hover,\n                        &:focus {\n                            cursor: pointer;\n                            opacity: 1;\n                        }\n                    }\n    \n               }\n    \n               & > .submit {\n                    width: 100%;\n                    height: 100%;\n    \n    \n                    & button {\n                        border: none;\n                        \n                        width: 100%;\n                        height: 100%;\n    \n                        border-bottom-right-radius: 10px;\n    \n                        background-color: var(--read-clr);\n                        opacity: 0.5;\n    \n                        color: aliceblue;\n    \n                        font-weight: bold;\n\n                        font-size: 20px;\n\n                        display: flex;\n                        justify-content: center;\n                        align-items: center;\n                        gap: 5px;\n\n                        & svg {\n                            height: 35px;\n                            width: 35px;\n                            \n                            fill: aliceblue;\n                        }\n    \n                        &:hover,\n                        &:focus {\n